import React, { FC } from 'react';
import * as S from '../lib/styles/Menu-desktop-styles';
import { LinkMain } from '../../../../../core/utils/enum/links/link-main';

type MenuDesktopType = {};

export const MenuDesktop: FC<MenuDesktopType> = ({}) => {
  return (
    <S.MenuDesktop>
      <S.List>
        <S.Item>
          <S.LinkElement to={LinkMain.Home}></S.LinkElement>
        </S.Item>

        <S.Item>
          <S.LinkElement to={LinkMain.NEWS}></S.LinkElement>
        </S.Item>

        <S.Item>
          <S.LinkElement to={LinkMain.APPEAL}></S.LinkElement>
        </S.Item>

        <S.Item>
          <S.LinkElement to={LinkMain.CONTACTS}></S.LinkElement>
        </S.Item>

        <S.Item>
          <S.LinkElement to={LinkMain.ADMIN}></S.LinkElement>
        </S.Item>
      </S.List>
    </S.MenuDesktop>
  );
};
