import * as yup from 'yup';

export const ValidationPersonalDataSchema = yup.object().shape({
  firstName: yup
    .string()
    .required('  ')
    .max(30, '   30'),

  lastName: yup
    .string()
    .required('  ')
    .max(30, '   30'),

  surname: yup
    .string()
    .required('  ')
    .max(30, '   30'),

  email: yup
    .string()
    .required('  ')
    .email('   '),

  phone: yup
    .string()
    .required('  ')
    .min(16, '    '),

  street: yup.string().required('  '),

  house: yup.string().required('  '),

  apartment: yup.string().required('  '),

  car: yup.string().required('  '),

  parkingSpace: yup.string().required('  '),

  contacts: yup.array().of(yup.string().required('  ')),
});
