import * as yup from 'yup';

export const ValidationFormPersonData = yup.object().shape({
  firstName: yup
    .string()
    .required('  ')
    .max(30, '   30'),
  lastName: yup
    .string()
    .required('  ')
    .max(30, '   30'),

  surname: yup
    .string()
    .required('  ')
    .max(30, '   30'),

  street: yup
    .string()
    .required('  ')
    .max(30, '   30'),

  house: yup
    .string()
    .required('  ')
    .max(30, '   30'),

  apartment: yup
    .string()
    .required('  ')
    .max(30, '   30'),

  email: yup
    .string()
    .required('  ')
    .email('   '),

  phone: yup
    .string()
    .required('  ')
    .min(16, '    '),

  car: yup
    .string()
    .required('  ')
    .max(30, '   30'),

  parking_space: yup
    .string()
    .required('  ')
    .max(30, '   30'),

  contacts: yup.array().of(yup.string().required('Contact is required')),
});
