import * as yup from 'yup';

export const ValidationFormModerator = yup.object().shape({
  firstName: yup
    .string()
    .required('  ')
    .max(30, '   30'),
  lastName: yup
    .string()
    .required('  ')
    .max(30, '   30'),

  surname: yup
    .string()
    .required('  ')
    .max(30, '   30'),

  position: yup
    .string()
    .required('  ')
    .max(30, '   30'),

  area: yup
    .string()
    .required('  ')
    .max(30, '   30'),

  email: yup
    .string()
    .required('  ')
    .email('   '),

  phone: yup
    .string()
    .required('  ')
    .min(16, '    '),
});
